import {
  PlatformShowError,
  StatusCode,
  StatusCodeNumeric,
} from '@wix/error-handler-core/types';
import { ErrorHandlerTestkitPublicAPI } from './ErrorHandlerTestkitPublicAPI';
import { initI18n } from '@wix/fe-essentials/i18n';
import { ErrorHandlerTestkitBase } from './ErrorHandlerTestkitBase';

export interface ErrorHandlerTestkitParams {
  language: string;
}

export class ErrorHandlerTestkit implements ErrorHandlerTestkitPublicAPI {
  readonly base;

  constructor(params: ErrorHandlerTestkitParams) {
    this.base = new ErrorHandlerTestkitBase({
      ...params,
      createI18n: (i18nOptions) =>
        initI18n({
          ...i18nOptions,
          locale: params.language,
        }),
    });
  }

  get showErrorMock() {
    return this.base.showErrorMock;
  }

  showError: PlatformShowError = (props) => {
    this.base.showError(props);
  };

  getCommonErrorMessageByStatus = async (
    status: StatusCode | StatusCodeNumeric,
  ): Promise<string | undefined> => {
    return this.base.getCommonErrorMessageByStatus(status);
  };

  async getClientErrorMessages() {
    return this.base.getClientErrorMessages();
  }
}
